@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: white;
}


.sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 5px;
    grid-gap: 20px;
    gap: 20px;

    width: 60px;
    height: 1080px;
    left: 0px;
    top: 0px;

    background: #003031;
}

.sidebar > img {
    width: 50px;
    height: 50px;
}
.sidebar__email{
    cursor: pointer;
}

.sidebar > hr {
    border: 0.1px solid #863654;
    padding-left: 50px;
    background-color: #863654;
}

.sidebar > icon {
    width: 30px;
    height: 30px;
}
.sidebar > icon{
    width: 30px;
    height: 30px;
}
.createHeader {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    
}
.createHeader__left {
    flex: 0.9 1;
}

.createHeader__right {
    
}
.input__title {
    margin-top: 2%;
    
}


.input__select {
    margin-top: 2%;
    display: flex;
    justify-content: space-evenly;
}
.create {
    display: flex;
    flex-direction: column;
    flex: 0.9 1;
    margin-left: 60px;
}
.app {
  display: flex;
  background-color: #ffffff;
}
.app__body {
  display: flex;
  flex: 1 1;
}

@media only screen and (max-width: 600px) {
  .app {
    display: flex;
    background-color: lightskyblue;
  }
}

.login {
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
    padding-bottom: 100px;
}

.login > img {
    object-fit: contain;
    height: 70px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.login > form {
    display: flex;
    flex-direction: column;
}

.login > form > input {
    border-color: #0c8d8f;
    width: 350px;
    height: 50px;
    font-size: 20px;
    padding-left: 10px;
    padding-bottom: 10px;
    border-radius: 5px;
}

.login > form > button {
    width: 365px;
    height: 50px;
    font-size: large;
    color: #fff;
    background-color: #863654;
    border-radius: 5px;
    cursor: pointer;
}

.login__register {
    color: #863654;
    cursor: pointer;
}

.login > p {
    margin-top: 20px;
}
.header {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  }
  .header__left {
    display: flex;
    align-items: center;
    flex: 0.9 1;
  }
  .header__left > form {
      margin-left: 20px;
  }
  
  .header__right {
    display: flex;
    align-items: center;
  }
  
.removeModal {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  background-color: white;
  padding: 1rem;
  text-align: center;
  width: 30rem;
  z-index: 10;
  position: fixed;
  top: 20vh;
  left: calc(50% - 15rem);
  color: #863654;
}

.btn {
  font: inherit;
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  border-radius: 4px;
  background-color: #800040;
  color: white;
  border: 1px solid #800040;
  margin: 0 1rem;
}
.btn:hover {
  background-color: #9c1458;
  border-color: #9c1458;
}
.btn--alt {
  background-color: transparent;
  color: #800040;
}
.btn--alt:hover {
  background-color: #f8dae9;
}
.backdrop {
    position: fixed;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.75);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }
.test-cases {
    display: flex;
    flex-direction: column;
    flex: 0.9 1;
    margin-left: 60px;
}

.table-head {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    background-color: #DBEAF4;
}
.table-head__title {
    display: flex;
    flex: 0.9 1;
    cursor: pointer;
}
.table-head__requirement {
    display: flex;
    flex: 0.3 1;
    cursor: pointer;
}
.table-head__assignee {
    display: flex;
    flex: 0.3 1;
    cursor: pointer;
}
.table-head__run {
    display: flex;
    flex: 0.2 1;
    cursor: pointer;
}
.table-head__status {
    display: flex;
    flex: 0.2 1;
    cursor: pointer;
}
.case-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}
.case-item__title {
    display: flex;
    flex: 0.9 1;
}
.case-item__requirement {
    display: flex;
    flex: 0.3 1;
}
.case-item__assignee {
    display: flex;
    flex: 0.3 1;
}
.case-item__run-no {
    display: flex;
    flex: 0.2 1;
    color: gray;
}
.case-item__run-green{
    display: flex;
    flex: 0.2 1;
    color: lightgreen;
}
.case-item__run-red{
    display: flex;
    flex: 0.2 1;
    color: red;
}
.case-item__status {
    display: flex;
    flex: 0.2 1;
}
.suite-cases {
    display: flex;
    flex-direction: column;
    flex: 0.9 1;
    margin-left: 60px;
}
